@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@500&display=swap);
body,html,.container{width:100vw;height:100vh;font-family:'Roboto', sans-serif}html{overflow:hidden}body{margin:0}ul{margin:0;padding:0}.container{display:flex}

.note{display:flex;justify-content:space-between;padding:10px 20px;position:relative;color:#FFFFFF;cursor:pointer}.note--selected{background-color:#FDD760;color:#150B03}.note__actions{border:none;background-color:#EECD63;padding:0;position:relative;width:20px;height:20px;border-radius:4px;margin-left:20px}.note__actions::after{display:flex;position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);color:#FFFFFF;content:'\2807'}

.popup{position:absolute;right:50px}

.list{background-color:#252525;width:15%;height:100%;overflow-y:scroll}.list::-webkit-scrollbar{width:5px;background-color:#252525}.list::-webkit-scrollbar-thumb{background-color:#FDD760;border-radius:9em}.list__btn-add{display:flex;align-items:center;justify-content:center;width:100%;padding:0;background-color:transparent;border:none;font-size:20px;color:#FDD760;cursor:pointer}

.search{position:absolute;bottom:30px;left:20px}.search__input{border:none;border-radius:4px;background:#343233;color:#FFFFFF}.search__input:focus{outline:none}

.content{display:flex;flex-direction:column;width:100%;border:none;outline:none}.content__title{flex-grow:1}.content__field{width:100%;flex-grow:40}.content__field:first-line{color:red}

